<template>
    <div class="output-box" v-if="isShow">
        <div class="output-title-line">
            <span class="output-title-msg">{{ signal }}{{ $t('inputsetting.configuration') }}</span>
            <span class="output-box-close" @click="cancel">
                <svgloader class="icon-close" icon="Close" size="1.2rem"></svgloader>
            </span>
        </div>
        <div class="inner-box">
            <div class="output-use-line">
                <div class="output-use-text">{{ $t('dlginputset.useConfig') }}</div>
                <LeetxSwitch :value="isUse" class="program-use-btn" :height="2" v-model="isUse"></LeetxSwitch>
            </div>
            <div class="output-system-line">
                <div class="output-system-text">{{ $t('outputsetting.system') }}</div>
                <NewSelect class="output-select-system" :realVal="activeGroup" :options="groups"
                    @valChng="updateGroup($event)"></NewSelect>
            </div>
            <div class="output-signal-line">
                <div class="output-signal-text">{{ $t('outputsetting.singnal') }}</div>
                <OptionalSelect class="output-select-signal" :realVal="outputSetting.LinkSdo"
                    :options="ownOptions[activeGroup]" @valChng="outputSetting.LinkSdo = $event" :isValid="isValid" :validReason="validReason"></OptionalSelect>
                <!-- <NewSelect class="output-select-signal" :realVal="outputSetting.LinkSdo" :options="outputData[activeGroup]" @valChng="outputSetting.LinkSdo=$event"></NewSelect> -->
            </div>
        </div>

        <div class="output-operate-line">
            <div class="output-cancel-btn" @click="cancel">{{ $t('outputsetting.cancel') }}</div>
            <div class="output-cnfrm-btn" @click="validSubmit">{{ $t('outputsetting.confirm') }}</div>
        </div>
    </div>
</template>
<script>
import { messageApi } from '../../../components/message/message'
// import InputVal from '../../../components/input/InputVal.vue'
// import CheckCircle from '../../../components/controls/CheckCircle.vue'
import svgloader from '../../../components/icon_font/svgloader.vue'
import NewSelect from '../../../components/select/NewSelect.vue'
import LeetxSwitch from '../../../components/switch/LeetxSwitch.vue'
import { vuexStore } from '../../../store/storeHelper'
import { deepCopy } from '../../../utils/deepCopy'
import { getOutputData, getOutputKey } from '../../../utils/io'
import OptionalSelect from '../../../components/select/OptionalSelect.vue'
import { maskApi } from '../../mask/StackMask.vue'

export default {
    components: {
        svgloader,

        NewSelect,
        LeetxSwitch,
        OptionalSelect
    },
    data() {
        return {
            outputSetting: {
                LinkSdo: "0x11101",
                isDelayOtpt: 0,
                delayTime: 0,
                StartByte: '0.0',

            },
            isUse: 0,
            outputData: getOutputData(),
            isShow: true,
            groups: {},
            activeGroup: '213312',
            oldLinkSdo: undefined,
            ownOptions: undefined,
            maskId: 0,
        }
    },
    methods: {
        setupIoInput(isSame) {
            this.isShow = false
            this.onConfirm && this.onConfirm({ data: this.outputSetting, isUse: this.isUse, isSame })
            maskApi.descend(this.maskId)
        },
        cancel() {
            this.isShow = false
            this.onCancel && this.onCancel()
            maskApi.descend(this.maskId)
        },
        updateData(key, data) {
            this.outputSetting[key] = data.sensorValue

        },
        getUnit(name) {
            return vuexStore.state.sensors[name]
        },
        updateGroup(group) {
            this.activeGroup = group
            for (let key in this.outputData[group]) {
                this.outputSetting.LinkSdo = this.outputData[group][key].val
                break
            }
        },
        initData() {
            this.isUse = this.isActive
            this.outputSetting = deepCopy(this.otptData)
            this.ownOptions = deepCopy(this.outputOptions)
            // 记录旧的信号值
            this.oldLinkSdo = this.outputSetting.LinkSdo
            for (let key in this.ownOptions.moudles) {
                this.groups[this.ownOptions.moudles[key]] = this.ownOptions.moudles[key]
            }
            let group = getOutputKey(this.oldLinkSdo).group
            let name = getOutputKey(this.oldLinkSdo).name
            // 当前配置里面的信号更新为可用
            if (!this.ownOptions[group][name].optional && this.isActive) {
                this.ownOptions[group][name].optional = true
            }
        },
        initStatus() {
            this.maskId = maskApi.rise(15, this.cancel)
        },
        setGroup() {
            this.activeGroup = getOutputKey(this.oldLinkSdo).group
            // for(let key1 in this.outputData){
            //     if(this.outputData[key1] instanceof Object){
            //         for(let key2  in this.outputData[key1]){
            //             if(this.outputData[key1][key2]==this.outputSetting.LinkSdo){
            //                 this.activeGroup=key1
            //                 break
            //             }
            //         }
            //     }
            // }
        },
        validSubmit() {
            let group = getOutputKey(this.outputSetting.LinkSdo).group
            let name = getOutputKey(this.outputSetting.LinkSdo).name
            let isSame = this.outputSetting.LinkSdo == this.oldLinkSdo ? 1 : 0
            //关闭配置或者信号是可以用的时候提交成功
            if (!this.isUse || this.ownOptions[group][name].optional) {
                this.setupIoInput(isSame)
            } else {
                let msgOne = this.$t('dlginputset.configUsing')
                let msgTwo = this.$t('dlginputset.seletAgain')
                messageApi.error([msgOne, msgTwo])
            }
        }
    },

    computed:{
        isValid(){
            let group=getOutputKey(this.outputSetting.LinkSdo).group
            let name=getOutputKey(this.outputSetting.LinkSdo).name
            //关闭配置或者信号是可以用的时候提交成功
            return this.ownOptions[group][name].optional
        },
        validReason(){
            return this.$t('dlginputset.configUsing')
        }
    },
    props: {
        otptData: {
            type: Object,
            default: function () {
                return {
                    LinkSdo: "0x22202",
                    Group: "GROUP2",
                    Not: 0,
                    BitLength: 1,
                    StartByte: '0.0',
                }
            }
        },
        signal: {
            type: String,
            "default": () => { return `I?${this.$t('dlginputset.config')}` }
        },
        isActive: {
            type: Number,
            default: 0,
        },
        outputOptions: {
            type: Object,
            default: function () {
                return getOutputData()
            }
        },
    },
    mounted() {
        this.initData()
        this.setGroup()
        this.initStatus()
    },
    watch: {
        otptData: {
            immediate: true,
            deep: true,
            handler: function () {
                this.initData()
                this.setGroup()
            }
        },
        outputOptions: {
            immediate: true,
            deep: true,
            handler: function () {
                this.initData()
                this.setGroup()
            }
        },
        isActive: {
            immediate: true,
            handler: function () {
                this.isUse = this.isActive
            }
        },
    }
}
</script>
<style scoped>
