    display: inline-block;
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    width: 3rem;
    height: 3rem;
    color: var(--C65);
}

.icon-close {
    position: absolute;
    right: 1.0625rem;
    top: 50%;
    transform: translateY(-50%);
}

