    position: absolute;
    width: 100%;
    top: 4rem;
    height: 3.2688rem;
}

.program-use-btn {
    position: absolute;
    font-size: var(--font1_625);
    top: 50%;
    transform: translateY(-50%);
    left: 16.6875rem;
}

.output-system-line {
    position: absolute;
    width: 100%;
    top: 8rem;
    height: 3.2688rem;
}

.output-is-delay {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 0.5rem;
}

.output-use-text,
.output-system-text,
.output-signal-text,
.output-delay-text {
    position: absolute;
    left: 7.23rem;
    top: 50%;
    transform: translateY(-50%);
    height: 1.5625rem;
    font-size: var(--font1_625);
    line-height: 1.5625rem;
    color: var(--C3B);
}

.output-select-system,
.output-select-signal {
    position: absolute;
