    text-align: center;
    font-size: 1.125rem;
    right: 3.2106rem;
    border-radius: 0.25rem;
    background-color: var(--fff);
    color: var(--C5A);
    border: 1px solid var(--Cf1);
}

.output-signal-line {
    position: absolute;
    width: 100%;
    top: 12.4375rem;
    height: 3.2688rem;
}

.output-delay-line {
    position: absolute;
    width: 100%;
    top: 16.875rem;
    height: 3.2688rem;
}

