<template>
    <div :class="{'select':true,'hight-light':isShow,'valid-select':!isValid}">
        <input :id="sltId" class="select-val" type="text" readonly @click="showOptions" @blur="onBlur(true)" v-model="selectValue" />
        <div  class="btn" @click="showOptions">
            <svgloader icon="Vector" :class="['arrow',{ 'rotate': rotate }]" :size="btnSize"></svgloader>
        </div>
        <div class="mask-box" v-show="isShow">
            <div :class="{'options-box':true,'isScroll':isScroll}">
                <div :class="{'option-text':true,'disable-option':!item.optional}" v-for="(item, key) in options" :key="key" @mousedown="valChng(key)" >
                    {{key}}
                </div>
            </div>
        </div>
        <span class="valid-reason" v-show="!isValid">{{validReason}}</span>

    </div>
</template>
<script>
import {messageApi} from '../message/message.js'

import svgloader from '../icon_font/svgloader.vue'
export default {
  components: { svgloader },
    props: {
        options: {
            type: Object,
            default: () => {
                return {}
            }
        },
        // 将该prop值作为selV的初始值
        realVal: { 
          type:null,
          default: ""
        },
        color: {
            type: String,
            default: () => {
              return 'blue'
            }
        },
        isReturnKey:{
            type:Boolean,
            default:false
        },
        maxSize:{
            type:Number,
            default:5,
        },
        height:{
            type:Number,
            default:3.125
        },
        width:{
            type:Number,
            default:17.625
        },
        fntSize:{
            type:String,
            default:'1.75rem'
        },
        btnSize:{
            type:String,
            default:'1.2rem'
        },
        isValid:{
            type:Boolean,
            default:true,
        },
        validReason:{
            type:String,
            default:""
        }
    },
    data () {
        return {
            selectValue:"",
            rotate: false,
            isShow: false,
            sltId:`slt-${Math.random().toFixed(6)*1000000}`,
            isAllowClose:true,
        }
    },
    methods: {
        showOptions () {
            this.isShow = !this.isShow
            this.rotate = !this.rotate
        },
        valChng (key) {
            let val=this.options[key].val

            if(this.options[key].optional){
                this.selectValue = key
                this.onBlur(this.isAllowClose)
                this.$emit('valChng',this.isReturnKey?key:val)
            }else{
                this.isAllowClose=false
                let msg=this.$t('dlginputset.configUsing')
                messageApi.error([msg])
                setTimeout(()=>{
                    this.isAllowClose=true
                },200)
            }

        },
        onBlur (isAllowClose) {
            if(isAllowClose){
                this.isShow = false
                this.rotate = false
            }

        },
        getSize(){
            let minPct=Math.min(window.innerWidth/800,window.innerHeight/1280).toFixed(3)
            return minPct*16
        },
        initData(){
            if(!this.isReturnKey){
                for(let key in this.options){
                    if(this.options[key].val==this.realVal){
                        this.selectValue=key
                    }
                }
            }else{
                this.selectValue=this.realVal
            }
        },
    },
    computed:{
        optiponsHeight(){
            let allSize=Object.keys(this.options).length
            let size=allSize >this.maxSize?this.maxSize:allSize
            let height=(size*(this.getSize()*this.height+2))/this.getSize()
            return height+'rem'
        },
        optiponsWidth(){
            return ((this.width*this.getSize()+18)/this.getSize()+'rem')
        },
        maskWidth(){
            return (this.width+'rem')
        },
        singleHeight(){
            return (this.height+'rem')
        },
        innerWidth(){
            return ((this.width*this.getSize()-4)/this.getSize())+'rem'
        },
        singleWidth(){
            return (this.width+'rem')
        },
        textWidth(){
            return ((this.width*this.getSize()-4)*0.96/this.getSize())+'rem'
        },
        isScroll(){
            let allSize=Object.keys(this.options).length
            return allSize >this.maxSize
        },


    },
    created() {
        this.initData()
    },
    watch:{
        options:{
            deep:true,
            immediate:true,
            handler:function(){
                this.initData()
            }
        },
        realVal:{
            deep:true,
            immediate:true,
            handler:function(){
                this.initData()
            }
        }
    }
}
</script>
<style scoped>
*{
    font-size: v-bind(fntSize) !important;
}
.select {
    box-sizing: border-box;
    width: v-bind(singleWidth) !important;
    line-height: v-bind(singleHeight) !important;
    border-radius:4px;
    border:1px solid var(--C65) !important;
}
.hight-light{
    border:2px solid var(--C00c155) !important;
    box-shadow: 0px 4px 10px 0px rgba(0, 0, 0, 0.3);
}
.select-val {
    width: v-bind(innerWidth);
    height:calc(100% - 2px);
    text-indent: 1rem;
    line-height: v-bind(singleHeight) !important;
    text-align: center;
    cursor: pointer;
    border: none;
    outline:none;
    color: var(--C65);
    position: relative;
    z-index: 31;
    background-color: transparent;

}
.btn{
    position: absolute;
    top: 30%;
    transform: translateY(-50%);
    right: 1rem;
    z-index: 30;
}
.arrow {
    position: absolute;
    right: 0px;
}
.rotate {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    transform: rotate(180deg);
}
.mask-box{
    position: absolute;
    top: calc(100% + 6px);
    left: -2px;
    width: v-bind(singleWidth) !important;
    height:v-bind(optiponsHeight)  !important;
    overflow: hidden;
    box-sizing: border-box;
    box-shadow: 0px 4px 10px 0px rgba(0, 0, 0, 0.3);
    z-index:100;

}
.options-box {
    position: absolute;
    background: #FFFFFF;
    width: v-bind(optiponsWidth) !important;
    height: 100%;
    top: 0;
    left: 0;
    z-index:2;
    border:1px solid #dddddd;
    border-radius:4px;
    padding-bottom:6px;
    z-index:100;
}
.option-text {
    width:v-bind(textWidth) !important;
    height: v-bind(singleHeight);
    line-height: v-bind(singleHeight);
    cursor: pointer;
    color: var(--C65);
    font-size: var(--font1_75);
    border-radius:2px;
    text-align: center;
    color: #3B3B3B;
}
.disable-option{
    color:var(--Cb0) !important;
}
.option-text:active{
    color:var(--C00);
}
.isScroll{
    overflow-y: scroll;
}
.valid-select{
    border: 2px solid red !important;
    box-shadow: -1px 1px 4px red !important;
}
.valid-reason{
    display: inline-block;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: calc(100% - 6px);
    padding: 1px;
    white-space: nowrap;
    color: red;
    font-size: 1rem !important;
}
</style>