import axios_ins from "../../utils/axios_ins";

export function busIoCfgUploadApi(){
    "获取NC IO配置"
    return axios_ins({
        url:'bus/io/cfg/upload',
        method:'get',
    })
}
export function busFieldbusCfgUploadApi(){
    "获取NC 总线配置"
    return axios_ins({
        url:'bus/fieldbus/cfg/upload',
        method:'get',
    })
}
export function busFiledbusItemsInApi(){
    return axios_ins({
        url:'bus/filedbus/items/in',
        method:'get',
    })
}

export function busFiledbusItemsOutApi(){
    return axios_ins({
        url:'bus/filedbus/items/Out',
        method:'get',
    })
}
export function busIoItemsInApi(){
    return axios_ins({
        url:'bus/io/items/in',
        method:'get',   
    })
}

export function busIoItemsOutApi(){
    return axios_ins({
        url:'bus/io/items/out',
        method:'get',   
    })
}

export function busIoModifyApi(paras){
    return axios_ins({
        url:'bus/io/modify',
        method:'post',
        data:paras   
    })
}

export function busFiledbusModifyApi(paras){
    return axios_ins({
        url:'bus/filedbus/modify',
        method:'post',
        data:paras   
    })
}

export function busIoCfgDownloadApi(paras){
    return axios_ins({
        url:'bus/io/cfg/download',
        method:'post',
        data:paras   
    })
}

export function busFiledbusCfgDownloadApi(paras){
    return axios_ins({
        url:'bus/filedbus/cfg/download',
        method:'post',
        data:paras   
    })
}

export function busIoDebugInApi(){
    return axios_ins({
        url:'bus/io/debug/in',
        method:'put',
    })
}

export function busIoDebugOutApi(){
    return axios_ins({
        url:'bus/io/debug/out',
        method:'put',
    })
}